body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Hind", sans-serif;
}

body {
  background-image: linear-gradient(
      to bottom,
      rgba(16, 17, 20, 1) 0%,
      rgba(16, 17, 20, 0.666) 225px
    ),
    url(/static/media/hero.6b56f2ad.png);
  /* background-size: cover; */
  background-size: max(1920px, 100vw);
  /* background-size: contain; */
  background-repeat: no-repeat;
  background-color: rgb(16, 17, 20);
  color: white;
}

:root {
  --body-margin: 7vw;
  --color-primary: #ff0d57;
  --header-height: 140px;
}

.color-primary {
  color: #ff0d57;
  color: var(--color-primary);
}

.appear {
  -webkit-animation: appear 0.2s ease-in;
          animation: appear 0.2s ease-in;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 1200px) {
  body {
    background-size: auto 100vh;
  }
}

.image-item {
  border: 1px solid black;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  justify-items: center;
}

